<template>
  <div class="page-container">
    <!--  -->
    <div class="header">
      <img src="../assets/header_image.jpg" alt="Header Image"> <!--  Webpack  -->
      <div class="header-content">
        <div class="left">
          <h1><br></h1>
          <p>Discover the Power of<br>Partnership for<br>Sustainable Water Solutions</p> <!--  -->
        </div>
      </div>
    </div>

    <!--  -->
    <div class="admin">
      <button class="admin-login-button" @click="openLoginModal"></button> <!--  -->
    </div>

    <!--  -->
    <div v-if="isLoginModalVisible" class="login-modal">
      <div class="modal-content">
        <LoginForm @login-success="handleLoginSuccess" />
        <button class="login-close-button" @click="closeLoginModal"></button>
      </div>
    </div>

    <div class="description">
      
    </div>

    <!--  -->
    <div class="status-row">
      <div
        class="status-box"
        v-for="(label, index) in labels"
        :key="index"
        :class="{ clickable: label === '' || label === '' }"
        @click="handleStatusBoxClick(label)"
      >
      <h3>{{ currentMonth }}</h3>

        <p class="status-title">{{ label }}</p>

        <p class="status-number">
          {{
            label === ''
              ? listedCompanyCount
              : label === ''
                ? nonListedCompanyCount
                : ''
          }} 
        </p>

        <div class="status-note"></div>
      </div>
    </div>

    <!--  -->
    <div class="table-header-container">
      <!--  -->
      <div class="table-header">
        <i class="fas fa-list"></i> <span></span>
      </div>
    </div>

    <div class="grid-container">
      <!--  -->
      <div class="grid-item">
        <table>
          <tr><th colspan="2"></th></tr>
          <tr v-for="(company, index) in listedCompanies" :key="index">
            <td>{{ company.company_name }}</td>
          </tr>

          <!--  -->
          <tr v-if="firstTablePages > 1">
            <td colspan="2" class="pagination">
              <button v-if="firstTablePage > 1" @click="changePage('firstTable', firstTablePage - 1)"></button>
              <span>{{ firstTablePage }} / {{ firstTablePages }}</span>
              <button v-if="firstTablePage < firstTablePages" @click="changePage('firstTable', firstTablePage + 1)"></button>
            </td>
          </tr>
        </table>
      </div>

      <!--  -->
      <div class="grid-item">
        <table>
          <tr><th colspan="2"></th></tr>
          <tr v-for="(company, index) in nonListedCompanies" :key="index">
            <td>{{ company.company_name }}</td>
          </tr>
          <tr v-if="secondTablePages > 1">
            <td colspan="2" class="pagination">
              <button v-if="secondTablePage > 1" @click="changePage('secondTable', secondTablePage - 1)"></button>
              <span>{{ secondTablePage }} / {{ secondTablePages }}</span>
              <button v-if="secondTablePage < secondTablePages" @click="changePage('secondTable', secondTablePage + 1)"></button>
            </td>
          </tr>
        </table>
      </div>

      <!--  -->
      <div class="grid-item">
        <table>
          <tr><th colspan="2"></th></tr>
          <tr><td>20253</td></tr>
          <tr><td>202412</td></tr>

          <tr v-if="thirdTablePages > 1">
            <td colspan="2" class="pagination">
              <button v-if="thirdTablePage > 1" @click="changePage('thirdTable', thirdTablePage - 1)"></button>
              <span>{{ thirdTablePage }} / {{ thirdTablePages }}</span>
              <button v-if="thirdTablePage < thirdTablePages" @click="changePage('thirdTable', thirdTablePage + 1)"></button>
            </td>
          </tr>
        </table>
      </div>
    </div>

    <!--  -->
    <div class="footer">
      <div class="footer-overlay"></div>
      <img class="footer-bg" src="@/assets/footer_image.jpg" alt="Footer Image">
      <div class="footer-content">
        <a href=""></a>
        <a href="">/</a>
      </div>
    </div>
  </div>
</template>



<script>
import axios from 'axios';
import LoginForm from '@/components/LoginForm.vue';

export default {
  name: 'App',
  components: {
    LoginForm
  },
  data() {
    return {
      currentMonth: '',  // 
      labels: [
        '',
        '',
        ''
      ],
      listedCompanyCount: 0,  // 
      listedCompanies: [],    // 
      nonListedCompanyCount: 0,  // 
      nonListedCompanies: [],    // 
      firstTablePage: 1,         // 
      firstTablePages: 1,        // 
      secondTablePage: 1,        // 
      secondTablePages: 1,       // 
      thirdTablePage: 1,         // 
      thirdTablePages: 1,        // 
      isLoginModalVisible: false,  // 
    };
  },
  mounted() {
    // 
    const now = new Date();
    const year = now.getFullYear();
    const month = String(now.getMonth() + 1).padStart(2, '0');
    this.currentMonth = `${year}${month}`;

    // 
    this.fetchCompanies();
  },
  methods: {
    // 
    fetchCompanies() {
      // 
      axios.get('/api/company/listed-companies-count')  // 
        .then((response) => {
          this.listedCompanyCount = response.data.count;
          this.firstTablePages = Math.ceil(this.listedCompanyCount / 10); // 
        })
        .catch((error) => {
          console.error(':', error);
        });

      // 
      axios.get(`/api/company/listed-companies?page=${this.firstTablePage}`)
        .then((response) => {
          this.listedCompanies = response.data;  // 
        })
        .catch((error) => {
          console.error(':', error);
        });

      // 
      axios.get('/api/company/non-listed-companies-count')  // 
        .then((response) => {
          this.nonListedCompanyCount = response.data.count;
          this.secondTablePages = Math.ceil(this.nonListedCompanyCount / 10); // 
        })
        .catch((error) => {
          console.error(':', error);
        });

      // 
      axios.get(`/api/company/non-listed-companies?page=${this.secondTablePage}`)
      .then((res) => {
        this.nonListedCompanies = res.data.data; // 
        this.nonListedCompanyCount = res.data.total; // 
        this.secondTablePages = Math.ceil(this.nonListedCompanyCount / 10); // 
      });

      // 
      axios.get(`/api/company/non-listed-companies?page=${this.thirdTablePage}`)
        .then(response => {
          this.thirdTablePages = Math.ceil(response.data.count / 10);  // 
        });
    },

    // 
    changePage(table, page) {
      // 
      if (page < 1 || page > this[`${table}Pages`]) return; // 

      this[`${table}Page`] = page;
      this.fetchCompanies(); // 
    },

    // 
    openLoginModal() {
      this.isLoginModalVisible = true;
    },

    // 
    closeLoginModal() {
      this.isLoginModalVisible = false;
    },

    // 
    handleLoginSuccess() {
      this.closeLoginModal();  // 
      this.$router.push('/admin-page');  //  AdminPage
    },

    // /
    handleStatusBoxClick(label) {
      if (label === '') {
        this.$router.push('/dashboard');
      } else if (label === '') {
        this.$router.push('/lsdashboard');
      }
    }
  },
};
</script>


